import { h, VNode } from 'preact';
import { route } from 'preact-router';
import Select, { GroupBase } from 'react-select';
import { useFormik } from 'formik';
import { useState } from 'preact/hooks';
import { createPortal } from 'preact/compat';
import { Text, withText } from 'preact-i18n';
import { ExternalLinks } from 'src/shared/Constants';
import SuccessModal from '../../../../shared/components/Modal/components/SuccessModal';
import style from './style.module.scss';

type OptionsType = {
  label: VNode<unknown> | string;
  value: string;
};

const options: OptionsType[] = [
  {
    value: 'integrateOurBlockchain',
    label: <Text id="feedback.integrateOurBlockchain">Integrate our blockchain to Rubic</Text>
  },
  {
    value: 'multiChainSwapProtocol',
    label: <Text id="feedback.MultiChainSwapProtocol">Multi-chain swap protocol</Text>
  },
  {
    value: 'commerceDeal',
    label: <Text id="feedback.CommerceDeal">Commerce deal</Text>
  },
  {
    value: 'problemsMultiChainRouting',
    label: (
      <Text id="feedback.ProblemsMultiChainRouting">Problems with Rubic Multi-Chain Routing</Text>
    )
  }
];

type FeedbackProps = {
  yourName: string;
  emailAdress: string;
  telegram: string;
  companyOrProject: string;
  companyWebsiteURL: string;
  selectReason: string;
  yourMessage: string;
};

const Feedback = withText<FeedbackProps>({
  yourName: 'feedback.yourName',
  emailAdress: 'feedback.emailAdress',
  telegram: 'feedback.telegram',
  companyOrProject: 'feedback.companyOrProject',
  companyWebsiteURL: 'feedback.companyWebsiteURL',
  selectReason: 'feedback.selectReason',
  yourMessage: 'feedback.yourMessage'
})((props: FeedbackProps) => {
  const [modal, setModal] = useState(false);
  const container = document.getElementById('modals');

  const formik = useFormik({
    initialValues: {
      name: '',
      email: '',
      telegram: '',
      projectName: '',
      projectLinks: '',
      reason: '',
      description: ''
    },
    onSubmit: values => {
      fetch('https://api.rubic.exchange/api/email/partnership', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify(values)
      })
        .then(() => {
          setModal(true);
        })
        .catch(e => {
          console.error(e);
        });
    }
  });

  const dropdownValue = options.find(option => option.value === formik.values.reason) || '';

  return (
    <div class="feedback">
      {/* eslint-disable-next-line @typescript-eslint/no-explicit-any */}
      <form className={style.feedback} onSubmit={formik.handleSubmit as any} id="getWidgetForm">
        <fieldset class={style['feedback__group']}>
          <input
            class={style['feedback__input']}
            type="text"
            name="name"
            placeholder={props.yourName}
            required
            onChange={formik.handleChange}
            value={formik.values.name}
          />

          <input
            class={style['feedback__input']}
            type="email"
            name="email"
            placeholder={props.emailAdress}
            required
            onChange={formik.handleChange}
            value={formik.values.email}
          />

          <input
            className={style['feedback__input']}
            type="text"
            name="telegram"
            placeholder={props.telegram}
            required
            onChange={formik.handleChange}
            value={formik.values.telegram}
          />

          <input
            class={style['feedback__input']}
            type="text"
            name="projectName"
            placeholder={props.companyOrProject}
            required
            onChange={formik.handleChange}
            value={formik.values.projectName}
          />

          <input
            class={style['feedback__input']}
            type="text"
            name="projectLinks"
            placeholder={props.companyWebsiteURL}
            required
            onChange={formik.handleChange}
            value={formik.values.projectLinks}
          />
        </fieldset>

        <div style={{ position: 'relative' }}>
          <Select
            openMenuOnClick={true}
            options={options as unknown as GroupBase<string>[]}
            className="select"
            classNamePrefix="select"
            placeholder={props.selectReason}
            value={dropdownValue}
            onChange={val => formik.setFieldValue('reason', (val as { value: string }).value)}
            theme={theme => ({
              ...theme,
              borderRadius: 0,
              colors: {
                ...theme.colors,
                primary25: 'transparent',
                primary: 'transparent'
              }
            })}
          />
          <input
            name="reason"
            autoComplete="off"
            style={{ opacity: 0, height: 0, position: 'absolute', top: '40px', left: '30px' }}
            value={dropdownValue && dropdownValue?.value}
            required
          />
        </div>
        <textarea
          class={style['feedback__textarea']}
          name="description"
          placeholder={props.yourMessage}
          onChange={formik.handleChange}
          value={formik.values.description}
        />

        <div class={style['feedback__bottom']}>
          <button class={style['feedback__submit']} type="submit">
            <Text id="contact.submitMessage">Submit Message</Text>
            <img src="assets/icons/arrow-right.svg" alt="Submit" />
          </button>
          <p class={style['feedback__privacy']}>
            <Text id="contact.byClicking">
              By clicking the "Submit Message" button, you automatically agree to the
            </Text>
            <a
              class={style['feedback__privacy-link']}
              href={ExternalLinks.policy}
              target="_blank"
            >
              <Text id="contact.privacyPolicy">privacy policy</Text>
            </a>
          </p>
        </div>
      </form>

      {createPortal(
        <SuccessModal
          open={modal}
          onModalClose={() => {
            setModal(false);
            route('/', true);
          }}
          title={
            <Text id="modal.theApplicationHasBeenSubmitted">
              Thank you! The application has been submitted
            </Text>
          }
        />,
        container as Element
      )}
    </div>
  );
});

export default Feedback;
